body {
  background-image: url(../img/bg/bg-lines.png);
  background-repeat: repeat-y;
  background-position: center;
  background-size: initial;
  font-family: sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 1254px) {
  .container {
    width: 1224px;
    padding-left: 0;
    padding-right: 0;
    margin-left: auto;
    margin-right: auto;
  }
}
.flex-end {
  display: flex;
  justify-content: flex-end;
}
.flex-column-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.relative {
  position: relative;
}
.read-more-link,
a.read-more-link {
  color: #333333;
  text-decoration: none;
  font-family: 'League-Spartan', sans-serif;
  font-size: 9px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 5.4px;
  position: relative;
  padding-bottom: 10px;
  display: inline-block;
}
.read-more-link:after,
a.read-more-link:after {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  height: 2px;
  background-color: rgba(93, 168, 239, 0.7);
}
.title {
  color: #333333;
  font-family: 'Playfair Display', serif;
  font-size: 42px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.42px;
  text-align: center;
  margin-bottom: 20px;
}
.subtitle {
  display: block;
  color: #5da8ef;
  font-family: 'League-Spartan', sans-serif;
  font-size: 9px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 5.4px;
}
.title-desc {
  color: rgba(97, 97, 97, 0.6);
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0.35px;
  margin-bottom: 35px;
}
.desc-text {
  color: rgba(97, 97, 97, 0.6);
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  letter-spacing: 0.35px;
}
.vertical-text {
  color: rgba(11, 12, 17, 0.2);
  font-family: 'League-Spartan', sans-serif;
  font-size: 9px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 5.4px;
  transform: rotate(-90deg) translate(-100%, 0);
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: left top;
}
.social {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1254px) {
  .social.social--header {
    flex-direction: column;
  }
}
.social__item {
  margin-left: 15px;
  margin-right: 15px;
}
@media (min-width: 1254px) {
  .social--header .social__item {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 35px;
  }
  .social--header .social__item:last-child {
    margin-bottom: 0;
  }
}
.navigation-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1254px) {
  .navigation-wrapper {
